:root {
    --primary: #d81e5b;
    --secondary: #8c38ff;
    --dark: #131a26;
    --light: #eee;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: var(--light);
}

.App {
    width: 100%;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    padding: 16px;
    text-align: center;
}

.calculator1 {
    width: 150%;
    max-width: 500px;
    max-height: 500px;
    background-color: #fff;
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
    margin-bottom: 60px;

}

.display {
    padding: 16px;
    text-align: right;
    background-color: var(--dark);
    color: var(--light);
    font-size: 40px;
    font-weight: 300;
}

.display span {
    font-size: 20px;
    color: #888;
}

button {
    appearance: none;
    border: none;
    outline: none;
    color: #fff;
    font-size: 30px;
    padding: 16px;
    cursor: pointer;
    transition: 0.4s;
}

button:hover {
    opacity: 0.9;
}

.operators {
    display: flex;

}

.operators button {
    flex: 1 1 0%;
    background-color: var(--primary);
    font-weight: 700;
    font-size: 30px;
}

.digits {
    display: flex;
    flex-wrap: wrap;

}

.digits button {
    flex: 1 1 33.333% ;
    max-width: 33.333%;
    background-color: var(--dark);
}

.break-line {
    display: unset;
    border-top: 1px solid black;
    padding: 20px 0px;
    width: 100vh;
}